import API from "./core.api";

async function ajax (opt) {
  let data;
  try {
    data = await API[opt.name](opt);
  } catch (err) {
    return
  }
  return data
}

const link = {
  "trending": "https://docs.google.com/spreadsheets/d/1myqIf8EwrsCJacsf-4O2dJEgFFZpygSQv1vpBUB9qgQ/edit#gid=0",
  "tech": "https://docs.google.com/spreadsheets/d/1FaLkTxh_dYp_R0g_Jr60sAfLCi8Ft4wczU5WgxgtEa0/edit#gid=0",
  "health": "https://docs.google.com/spreadsheets/d/1vxd4MxxNG3FUGRLSVYTxDbmN38oPRQ-cz_tF4DMYyqU/edit#gid=0",
  "foodTravel": "https://docs.google.com/spreadsheets/d/1Y5HejmuKLvhfGguRsPo7bHUYk1rFbFfxB2Dc4vo-vcQ/edit#gid=0",
}

//Khởi tạo state mặc định
const initialState = () => ({
  records:[],
  record: {}
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();


/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  records: state => state.records,
  record: state => state.record,
}

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SET_ALL (state, data) {
    state.records = data
  },
  SET_UNION (state, data) {
    if(data && data.length){
      state.records = _.unionBy(state.records, data, 'key');
    }
  },
  SET (state, data) {
    state.record = data;
    state.records = _.unionBy(state.records, [data], 'key');
    console.log(state.records)
  },
  SET_BY_ID (state, dataId) {
    if(!dataId) return;
    let product = state.records.find((record) => record.id === dataId);
    if(product){
      state.record = product
    }
  },
  ADD (state) {
    state.records.push(state.record)
  },
  EDIT (state, data) {
    let records = state.records
    records.splice(records.indexOf(data), 1)
    state.records = records
    state.record = data
  },
  REMOVE (state, data) {
    let records = state.records
    records.splice(records.indexOf(data), 1)
  },
  CLEAN (state) {
    state.record = {}
  },

  //cài đặt lại state
  RESET (state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key]
    });
  },
}

import _ from 'lodash';
/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setAll: async (opt) => {
    let data = await ajax(opt)
  },
  setLitmit: async ({commit}, opt) => {
    let data;
    try {
      data = await API.GET_LIMIT({url: link[opt.cat], limit: opt.limit});
    } catch (err) {
      return
    }
    commit('SET_UNION', data)
  },
  set: async ({commit}, opt) => {
    let data;
    try {
      console.log(opt)
      data = await API.GET({url: link[opt.cat], key: opt.key});
    } catch (err) {
      return
    }
    commit('SET', data)
  },
  add: async ({commit}) => {
    commit('ADD')
  },
  edit: async ({commit}, data) => {
    commit('EDIT', data)
  },
  remove: async ({commit}, data) => {
    commit('REMOVE', data)
  },
  clean: async ({commit}) => {
    commit('CLEAN')
  },
  reset: async ({ commit }) =>  {
    commit('RESET');
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}