export function removeUnicodeSpace (str) {
  try {
    return str.toLowerCase()
      .replace(/[àáảãạâầấẩẫậăằắẳẵặ]/g, 'a')
      .replace(/[èéẻẽẹêềếểễệ]/g, 'e')
      .replace(/[đ]/g, 'd')
      .replace(/[ìíỉĩị]/g, 'i')
      .replace(/[òóỏõọôồốổỗộơờớởỡợ]/g, 'o')
      .replace(/[ùúủũụưừứửữự]/g, 'u')
      .replace(/[ỳýỷỹỵ]/g, 'y')
      .replace(/[`~!@#$%^&*()_|+\=?;:'",.<>\{\}\[\]\\\/]/gi,"")
      .replace(/ {2,}/g,' ')
      .replace(/ /g,'-');
  } catch {
    return ''
  }
}

export function capitalizeLetter(text) {
  if (!text) return ''
  return text.charAt(0).toUpperCase() + text.slice(1);
}