<template>
  <div class="home-page page">
    <div class="container" v-if="ready">
      
      <div v-viewability="{padded: 100, store: {dispatch: 'Core/setLitmit', opt: {cat: 'trending', limit: 12}}, callback: viewedLoad }">
        <agile :options="agileOpts" v-if="cores && cores.trending" v-once>
          <template v-for="(item, key) in cores.trending">
            <div class="slide" :key="key" v-if="item.pics[0].src && key < 6" @click="onClicked(item)">
              <div class="slide-box">
                <div class="slide-img">
                  <img v-lazy="item.pics[0].src" />
                </div>
                <h3>{{item.title | truncate(36, '...')}}</h3>
              </div>
            </div>
          </template>
        </agile>
      </div>

      <div class="row">
        <div class="col-xs-12 col-sm-6 col-md-4" v-viewability="{padded: 100, store: {dispatch: 'Core/setLitmit', opt: {cat: 'tech', limit: 5}}, callback: viewedLoad }">
          <h3 class="cate" v-if="cores && cores.tech" @click="nextCat('tech')">
            <span class="arrow-wrap">
              <i class="arrow right"></i>
            </span>
            <span>{{dcat['tech']}}</span>
          </h3>
          <template>
            <pieces :items="cores.tech" cat="tech" @clicked="onClicked"/>
          </template>
        </div>
        
        <div class="col-xs-12 col-sm-6 col-md-4" v-viewability="{padded: 100, store: {dispatch: 'Core/setLitmit', opt: {cat: 'health', limit: 5}}, callback: viewedLoad }">
          <h3 class="cate" v-if="cores && cores.tech" @click="nextCat('health')">
            <span class="arrow-wrap">
              <i class="arrow right"></i>
            </span>
            <span>{{dcat['health']}}</span>
          </h3>
          <template>
            <pieces :items="cores.health" cat="health" @clicked="onClicked"/>
          </template>
        </div>
        
        <div class="col-xs-12 col-sm-6 col-md-4" v-viewability="{padded: 100, store: {dispatch: 'Core/setLitmit', opt: {cat: 'foodTravel', limit: 5}}, callback: viewedLoad }">
          <h3 class="cate" v-if="cores && cores['food-travel']" @click="nextCat('food-travel')">
            <span class="arrow-wrap">
              <i class="arrow right"></i>
            </span>
            <span>{{dcat['food-travel']}}</span>
          </h3>
          <template>
            <pieces :items="cores['food-travel']" cat="food-travel" @clicked="onClicked"/>
          </template>
        </div>
      </div>

      <van-popup v-model="popup" class="vpopup" closeable @close="onClose">
        <template v-if="detail">
          <tin :item="detail" />
        </template>
      </van-popup>
    </div>

    <loader v-if="!ready" />
  </div>
</template>
<script>
import Pieces from "@/components/Pieces.vue";
import Tin from "@/components/Tin.vue";
import Loader from "@/components/Loader.vue";

export default {
  name: 'home-page',
  components: {
    Pieces,
    Tin,
    Loader
  },
  data() {
    return {
      ready: false,
      popup: false,
      detail: null,
      dcat: {
        "tech": "Công Nghệ",
        "health": "Sức Khoẻ",
        "food-travel": "Du Lịch & Ẩm Thực",
      },
      tabs: 0,
      limitSlides: 6,
      agileOpts:{
        slidesToShow: 2,
        centerMode: false,
        navButtons: false,
        autoplay: false,
        autoplaySpeed: 5000,
        responsive: [
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 3,
            },
          },
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 4,
            },
          },
          {
            breakpoint: 970,
            settings: {
              slidesToShow: 5,
            },
          },
        ]
      },
    };
  },
  computed: {
    core(){
      return this.$store.getters['Core/record']
    },
    cores(){

      let vm = this;
      let records = vm.$store.getters['Core/records'];

      let grouped = _.chain(records)
      .groupBy("cat")
      .value();

      return grouped;
    },
  },
  methods: {
    nextCat(cat){
      this.$router.push('/category/'+cat)
    },
    onClicked(data){
      let vm = this;
      vm.detail = data;
      vm.popup = true;
      if(history) history.pushState({}, data.key, '/#/detail/'+data.cat+'/'+data.key)
    },
    onClose(){
      if(history) history.go(-1)
    },
    redirectMenu(type){
      return this.$router.push('/menu?type='+removeUnicodeSpace(type))
    },
    viewedLoad(store){
      this.$store.dispatch(store.dispatch, store.opt)
    }
  },
  created(){
    let vm = this;

    vm.ready = true;
  }
  
};
</script>
