<template>
  <div class="pieces" v-if="items && items.length">
    <template v-for="(value, key) in items">
      <piece :item="value" :key="key" @clicked="next" />
    </template>
  </div>
</template>

<script>
import Piece from "@/components/Piece.vue";
export default {
  name: 'Pieces',
  props: ["items", "cat"],
  components: {
    Piece,
  },
  methods: {
    next(data){
      data.cat = this.cat;
      this.$emit('clicked' , data);
    }
  }
  
};
</script>