<template>
  <div class="detail-page">

    <div class="content container-fluid">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-3">
          
        </div>
        <div class="col-xs-12 col-sm-12 col-md-6">
          <tin :item="core" />
        </div>
        <div class="col-sm-12 col-md-3">
          
        </div>
      </div>

    </div>

    <!-- <loader v-if="ready" /> -->
  </div>
</template>
<script>
import _ from 'lodash';
import Tin from "@/components/Tin.vue";
import Loader from "@/components/Loader.vue";

export default {
  name: 'detail-page',
  components: {
    Tin,
    Loader
  },
  data() {
    return {
      ready: false,
      cat: null,
    };
  },
  computed: {
    core(){
      return this.$store.getters['Core/record']
    },
  },
  created(){
    let vm = this;
    vm.cat = vm.$route.params.cat;
    vm.key = vm.$route.params.key;
    vm.$store.dispatch('Core/set', {cat: vm.cat, key: vm.key})
    vm.ready = false;
  }
  
};
</script>
