<template>
  <div class="category-page page">

    <div class="content container-fluid" v-if="ready">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-3">
          
        </div>
        <div class="col-xs-12 col-sm-12 col-md-6" v-if="categories && categories.length">
          <h3 class="cate">
            <span class="arrow-wrap">
              <i class="arrow right"></i>
            </span>
            <span>{{dcat[cat]}}</span>
          </h3>
          <pieces :items="categories" :cat="cat" @clicked="onClicked"/>
        </div>
        <div class="col-sm-12 col-md-3">
          
        </div>
      </div>

      <van-popup v-model="popup" class="vpopup" closeable @close="onClose">
        <template v-if="detail">
          <tin :item="detail" />
        </template>
      </van-popup>

    </div>

    <loader v-if="!ready" />
  </div>
</template>
<script>
import _ from 'lodash';
import Pieces from "@/components/Pieces.vue";
import Tin from "@/components/Tin.vue";
import Loader from "@/components/Loader.vue";

export default {
  name: 'category-page',
  components: {
    Pieces,
    Tin,
    Loader
  },
  data() {
    return {
      ready: false,
      popup: false,
      cat: null,
      scat: null,
      dcat: {
        tech: "Công Nghệ",
        health: "Sức Khoẻ",
        foodTravel: "Du Lịch & Ẩm Thực",
      },
      detail: null
    };
  },
  computed: {
    categories(){
      let vm = this;
      if(vm.scat) return this.$store.getters[vm.scat+'/items']
      return null
    },
  },
  methods: {
    onClicked(data){
      let vm = this;
      vm.detail = data;
      console.log(data)
      vm.popup = true;
      if(history) history.pushState({}, data.key, '/#/detail/'+data.cat+'/'+data.key)
    },
    onClose(){
      let vm = this;
      if(history) history.pushState({}, vm.cat, '/#/category/'+vm.cat)
    }
  },
  async created(){
    let vm = this;
    vm.cat = vm.$route.params.cat;
    vm.scat = capitalizeLetter(vm.cat);
    console.log(vm.scat)
    await vm.$store.dispatch(vm.scat+'/setAll')
    vm.ready = true;
  }
  
};
</script>
