import API from "./hot.api";

function removeUnicodeSpace(str) {
  try {
    return str.replace(/[àáảãạâầấẩẫậăằắẳẵặ]/g, 'a')
      .replace(/[èéẻẽẹêềếểễệ]/g, 'e')
      .replace(/[đ]/g, 'd')
      .replace(/[ìíỉĩị]/g, 'i')
      .replace(/[òóỏõọôồốổỗộơờớởỡợ]/g, 'o')
      .replace(/[ùúủũụưừứửữự]/g, 'u')
      .replace(/[ỳýỷỹỵ]/g, 'y')
      .replace(/[`~!@#$%^&*()_|+\=?;:'",.<>\{\}\[\]\\\/]/gi,"")
      .replace(/ {2,}/g,' ')
      .replace(/ /g,'-');
  } catch {
    return ''
  }
}

//Khởi tạo state mặc định
const initialState = () => ({
  itemsSearch: [],
  items: [],
  //giá trị lưu tạm thời dùng cập nhật dữ liệu mới hoặc chỉnh sửa 
  item: {}
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();


/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  item: state => state.item,
  items: state => state.items,
  itemsSearch: state => state.itemsSearch,
}

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SET_ALL (state, data) {
    state.items = data
  },
  SET (state, data) {
    state.item = data
  },
  GET (state, key) {
    state.item = state.items.find((data) => data.key === key)
  },
  ADD (state) {
    state.item.time = new Date();
    state.items.push(state.item)
  },
  EDIT (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
    state.items = items
    state.item = data
  },
  REMOVE (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
  },
  CLEAN (state) {
    state.item = {}
  },

  //cài đặt lại state
  RESET (state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key]
    });
  },
}

/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setAll: async ({commit}) => {
    let data;
    try {
      data = await API.GET_ALL();
    } catch (err) {
      return
    }
    commit('SET_ALL', data)
  },
  setLimit: async ({commit}, limit) => {
    let data;
    try {
      data = await API.GET_LIMIT(limit);
    } catch (err) {
      return
    }
    commit('SET_ALL', data)
  },
  set: async ({commit}, key) => {
    let data;
    try {
      data = await API.GET(key);
    } catch (err) {
      return
    }
    commit('SET', data)
  },
  add: async ({commit}) => {
    commit('ADD')
  },
  edit: async ({commit}, data) => {
    commit('EDIT', data)
  },
  remove: async ({commit}, data) => {
    commit('REMOVE', data)
  },
  clean: async ({commit}) => {
    commit('CLEAN')
  },
  reset: async ({ commit }) =>  {
    commit('RESET');
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}